* {
  box-sizing: border-box;
}
body {
  margin: 0;
  background-image: url(./books.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.app {
  justify-items: center;
}
.container {
  margin: 30px auto;
  top: 50%;
  width: 500px;
  height: 570px;
  background-image: linear-gradient(gray, black);
  border-radius: 10px;
  color: white;
  border: none;
  padding: 10px;
  box-shadow: 0px 0px 10px 10px rgba(39, 38, 38, 0.808);
  overflow: hidden;
}
.flex a {
  text-decoration: none;
  color: white;
  margin-bottom: 40px;
  margin-left: 20px;
}
.flex {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
.addBtn {
  width: 60px;
  height: 60px;
  font-weight: 300;
  font-size: 30px;
  background-color: gray;
  /* opacity: 0.7; */
  color: white;
  border: none;
  border-radius: 50%;
}
.addBtn:hover {
  background-color: rgb(66, 75, 78);
  opacity: 1;
  cursor: pointer;
  font-size: 220%;
}
.date {
  font-size: 60px;
  margin-left: 20px;
}
.heading {
  font-size: 20px;
  width: 95%;
  height: 30px;
  border-radius: 10px;
  border: none;
  outline: none;
  font-weight: bolder;
  padding-left: 8px;
  margin: 10px;
  background-color: #f2f2f2;
}
.textarea {
  font-size: 15px;
  width: 95%;
  padding-left: 8px;
  font-weight: bold;
  border-radius: 10px;
  border: none;
  background-color: #f2f2f2;
  outline: none;
  margin: 10px;
}
.flexx {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
.backBtn {
  text-decoration: none;
  color: white;
  font-size: 30px;
  padding: 5px 10px;
  background-color: black;
  border-radius: 5px;
}
.table {
  padding: 0 20px;
  font-size: x-large;
}
.th {
  text-decoration: underline;
  font-style: italic;
}
footer {
  text-align: center;
  font-style: italic;
}
